$charcoal: #1D465C;
$light-sea-green: #12AEAB;
$verdigris: #0FA5A8;
$ming: #137280;
$magic-mint: #9FF4D3;
$verdigris-2: #0FA5A8;
$gunmetal: #1C3746;
$metallic-seaweed: #037B83;
$middle-blue-green: #B7EBDF;
$blush: #D7638A;
$leafGreen: #92CEC3;
$clearGreen: #8BBF5D;
body {
    background-color: #fff;
}

.btn-leafgreen {
    background-color: $leafGreen;
}

.btn-cleargreen {
    background-color: $clearGreen;
}

.bg-white {
    background-color: #fff;
    transition: background-color .2s ease-in;
}

.bg-leafgreen {
    background-color: #60A4A2;
    color: #fff;
}