    @import '_colors.scss';
    @import '_fonts.scss';
    @import 'aos.scss';
    html {
        box-sizing: border-box;
        scroll-behavior: smooth;
    }
    
    *,
    *::before,
    *::after {
        box-sizing: inherit;
        margin: 0;
        padding: 0;
    }
    
    body {
        background-color: #fff;
        overflow-x: hidden;
        height: 100%;
    }
    
    #home {
        background-color: unset;
        width: 100vw;
        height: 100vh;
        .bg-white .dl_link {
            color: #7cbe70 !important;
        }
        .dl_link {
            color: #ee732b !important;
        }
        nav {
            position: fixed;
            left: 0;
            top: 0;
            width: 100vw;
            z-index: 99;
            transition: background-color .2s ease-out;
            justify-content: center;
            .navbar-content {
                padding: 10px;
                justify-content: center;
                display: flex;
                // justify-content: space-between;
                align-items: center;
                vertical-align: middle;
            }
            .navbar-image {
                position: absolute;
                width: 100vw;
                z-index: -1;
            }
            .logo {
                display: inline-block;
            }
            .nav-links {
                font-size: 20px;
                list-style: none;
                display: flex;
            }
            .nav-item {
                padding: 0 25px;
                a {
                    display: inline-block;
                    text-decoration: none;
                    color: #000;
                    font-family: "Avant-Garde-Std-Book";
                    opacity: .65;
                    transition: all .3s ease;
                    padding-top: 14px;
                    &:hover {
                        opacity: 1;
                    }
                }
            }
            .logo img {
                max-height: 48px;
            }
            @media (max-width: 1199.98px) {
                background-color: #fff;
                .logo img {
                    height: unset;
                }
                .navbar-content {
                    justify-content: space-between;
                }
                .nav-links {
                    font-size: 1.8vw;
                    list-style: none;
                    display: flex;
                }
                .nav-item {
                    padding: 0 1.5rem;
                    a {
                        color: #2d2d2d;
                    }
                }
            }
        }
        #scene {
            position: absolute !important;
            z-index: -1;
            left: 0;
            top: 0;
            clip: rect(0px, calc(100vw + 80px), 100vh, 0px);
            max-height: 100vh;
            max-width: 100vw;
            overflow: hidden;
            background-color: #BCECED;
        }
        .parallaxe {
            min-width: calc(100vw + 80px);
            max-width: calc(100vw + 80px);
            min-height: calc(100vh + 120px);
            background-size: cover;
            background-position: center center;
            &.layerA {
                top: -40px !important;
                left: -40px !important;
                background-image: url("../img/home/cloudz.png");
            }
            &.layerB {
                left: -40px !important;
                top: -90px !important;
                background-image: url("../img/home/mountains.png");
            }
            &.layerC {
                left: -40px !important;
                top: -120px !important;
                background-image: url("../img/home/front-dude.png");
            }
        }
        .slogan {
            text-align: center;
            font-family: "Avant-Garde-Std-Book";
            color: #085963;
            h1 {
                font-size: 2rem;
            }
        }
        .buttons {
            text-align: center;
            z-index: 12;
            font-family: "Avant-Garde-Std-Book";
            font-size: 1.2rem;
            .btn-az {
                padding: 10px 55px;
                margin-right: 15px;
                color: white;
                border-radius: 15px;
                &:hover {
                    cursor: pointer;
                }
                &.primary {
                    background-color: #EE732B;
                    transition: all .1s ease-in;
                    &:hover {
                        padding: 12px 58px;
                        background-color: #FBA93B;
                        text-decoration: none;
                        transition: all .2s ease-out;
                    }
                }
                &.secondary {
                    background-color: #FBA93B;
                }
            }
        }
        .logoz {
            margin-top: 140px;
            max-height: 280px;
            max-width: 280px;
        }
        @media (max-width: 1366.98px) {
            .slogan {
                color: #fff;
            }
            .slogan,
            .buttons {
                font-size: 18px;
                .btn {
                    &.secondary {
                        display: unset;
                    }
                }
            }
            .vertical-align {
                display: none;
            }
        }
    }
    
    #ctafullscreen {
        background-color: #60A4A2;
        min-width: 100vw;
        min-height: 100vh;
        height: calc(100vh - 60px);
        .row {
            height: 100vh;
            background-color: #60A4A2;
        }
        // #app-visual {}
        h3 {
            // padding-top: 290px;
            font-family: "Avant-Garde-Std-Extra-Light";
            font-size: 35px;
        }
        .btn-az {
            font-family: "Avant-Garde-Std-Book";
            font-size: 24px;
            padding: 15px 40px;
            border: solid 3px white;
            color: white;
            text-decoration: none;
            transition: all .2s ease-in;
            &:hover {
                background-color: #fff;
                color: #60A4A2;
                transition: all .2s ease-out;
            }
        }
        .btn_dl {
            cursor: pointer;
        }
        #biche-bg {
            background-image: url('../img/biche.png');
            background-position: top right;
            background-size: contain;
            background-repeat: no-repeat;
        }
        @media (max-width: 1366.98px) {
            #app-visual {
                width: auto;
                height: 300px;
            }
        }
    }
    
    #card-zone {
        position: relative;
        padding-top: 310px;
        .leaf-separator {
            position: absolute;
            top: 0;
            background-image: url('../img/leaf_separator.png');
            background-repeat: no-repeat;
            background-size: contain;
            height: 386px;
            width: 100vw;
        }
        .row:first-child {
            padding-left: 15px;
        }
        .card {
            border: 1px solid #F8F8F8;
            border-radius: 25px;
            box-shadow: 0 5px 6px rgba(0, 0, 0, 0.25);
            transition: all .1s ease-out;
            &:hover {
                margin: -2px;
                transition: all .2s ease-in;
            }
        }
        .card h3 {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 600;
            font-size: 22px;
            color: #085963;
        }
        .card .card-text {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 200;
            font-size: 16px;
            line-height: 24px;
            color: #085963;
        }
        .btn-az {
            font-family: "Avant-Garde-Std-Book";
            padding: 10px 55px;
            margin-right: 15px;
            color: white;
            border-radius: 15px;
            font-size: 18px;
            transition: all .1s ease-in;
            &:hover {
                cursor: pointer;
            }
            &.darkgreen {
                background-color: #085963;
            }
            &:hover {
                padding: 12px 58px;
                background-color: #60A4A2;
                text-decoration: none;
                transition: all .2s ease-out;
            }
        }
        @media (max-width: 1366.98px) {
            padding-top: 210px;
            .row:first-child {
                padding-left: 35px;
            }
        }
    }
    
    #card-zone2 {
        position: relative;
        padding-top: 310px;
        padding-bottom: 50px;
        .frise-container {
            padding-top: 200px;
            padding-bottom: 200px;
            padding-left: 100px;
            padding-right: 100px;
        }
        .container-az {
            width: 100%;
            padding-right: 15px;
            padding-left: 15px;
            margin-right: auto;
            margin-left: auto;
        }
        .col-3 {
            padding-left: 0;
            padding-right: 0;
        }
        .row:first-child {
            padding-left: 15px;
        }
        .card {
            border-radius: 0;
            padding-top: 25px;
            .card-img-top {
                max-width: 50%;
                margin: auto;
            }
            .card-body {
                h3 {
                    padding-bottom: 25px;
                }
            }
        }
        .card h3 {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 600;
            font-size: 22px;
        }
        .card .card-text {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 200;
            font-size: 16px;
            line-height: 24px;
        }
        @media (min-width: 1367px) {
            .container-az {
                max-width: 1400px;
            }
        }
        @media (max-width: 1366.98px) {
            padding-top: 210px;
            .row:first-child {
                padding-left: 5px;
            }
        }
        .bottom-tree {
            position: absolute;
            bottom: 0;
            right: 0;
            background-image: url('../img/tree_bottom.png');
            background-repeat: no-repeat;
            background-size: contain;
            background-position-y: bottom;
            height: 100%;
            width: 100vw;
            z-index: -1;
        }
        .zone {
            opacity: .85;
            transition: all .5s ease-in;
            &:hover {
                cursor: pointer;
                opacity: 1;
                transform: scale(1.02) translate(-15px, -20px);
                transition: all .5s ease-out;
            }
            .to-hide {
                opacity: .5;
                transition: all .5s ease-in;
            }
            &:hover .to-hide {
                opacity: 1;
                transform: scale(1.02) translate(15px, 25px);
                transition: all .5s ease-out;
            }
        }
    }
    
    #animaux-vegetaux {
        min-width: 100vw;
        min-height: 100vh;
        background-image: url('../img/renardo.png');
        background-position: center center;
        background-size: cover;
        h3 {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 200;
            font-size: 42px;
            color: #fff;
        }
        .card .card-text {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 200;
            font-size: 16px;
            line-height: 24px;
            color: #fff;
        }
        .card {
            border-radius: unset;
            background-color: #224f53;
        }
        #flore-row,
        #faune-row {
            transition: all 1.5s ease;
        }
        .hidden-az {
            z-index: -999;
            position: absolute;
            top: 0;
            opacity: 0;
            transition: all 1.5s ease;
        }
        @media (min-width: 1367px) {
            .card {
                margin-top: 100px;
            }
            #faune {
                margin-top: 75px;
            }
            #flore {
                margin-top: 105px;
            }
        }
        @media (max-width: 1366.98px) {
            #faune {
                margin-top: 95px;
            }
            #flore {
                margin-top: 125px;
            }
        }
    }
    
    #choixsentier {
        h3 {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 200;
            font-size: 42px;
            color: #224f53;
        }
        .card {
            background-color: #60A4A2;
            text-align: center;
        }
        .card h3 {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 600;
            font-size: 22px;
            color: #fff;
        }
        .card .card-text {
            font-family: "Avant-Garde-Std-Book";
            font-weight: 200;
            font-size: 16px;
            line-height: 24px;
            color: #fff;
        }
        .btn-az {
            font-family: "Avant-Garde-Std-Book";
            padding: 10px 55px;
            margin-right: 25px;
            margin-left: 25px;
            color: white;
            border-radius: 15px;
            font-size: 18px;
            &:hover {
                cursor: pointer;
            }
            &.darkgreen {
                background-color: #297067;
            }
            &:hover {
                padding: 12px 58px;
                background-color: #224f53;
                text-decoration: none;
                transition: all .2s ease-out;
            }
        }
        .secteur:hover path,
        .secteur:hover polygon,
        .secteur:hover polyline {
            cursor: pointer;
            fill: #FBA93B !important;
        }
        .secteur.selected path,
        .secteur.selected polygon,
        .secteur.selected polyline {
            fill: #FBA93B !important;
        }
        .no-pointer-events {
            pointer-events: none;
        }
    }